.form__section {
  position: relative;
  padding-top: 30px;

  &--hr {
    padding-bottom: 30px;
  }

  &--hr::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: -9999px;
    right: -9999px;
    border-top: 1px solid $border-color;
  }
}

.form__bottom {
  padding-top: 30px;
}

.form__section-title {
  @include heading(24px);
  position: relative;
  margin-top: 0;
  margin-bottom: 35px;

  span {
    display: inline-block;
    margin-left: -20px;
    padding: 0 20px;
    background-color: $white;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 14px;
    left: -9999px;
    right: -9999px;
    border-top: 2px solid $primary;
  }
}

.form__group {
  display: flex;
  align-items: center;

  &:not(:last-of-type) {
    margin-bottom: 15px;
  }

  &--required {
    .form__label::after {
      content: "*";
    }
  }

  &--reverse {
    .form__label {
      flex: 1 1 auto;
      margin-right: 0;
    }

    .form__control {
      flex: 0 0 auto;
      margin-right: 15px;
    }
  }
}

.form__label {
  @include heading(16px, $fw: 400, $color: $body-color);
  flex: 0 0 auto;
  text-transform: uppercase;
  margin-right: 15px;
}

.form__input {
  width: 100%;
  padding: 14px 16px;
  border: 1px solid $input-border;
}

.form__input-radio {
  display: grid;
  place-content: center;
  appearance: none;
  background-color: $white;
  margin: 0;
  font: inherit;
  width: 30px;
  height: 30px;
  border: 1px solid $input-border;
  border-radius: 50%;

  &::before {
    content: "";
    width: 14px;
    height: 14px;
    border-radius: 50%;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    box-shadow: inset 1em 1em $primary;
  }

  &:checked {
    &::before {
      transform: scale(1);
    }
  }
}

.form__control {
  flex: 1 1 auto;

  input,
  textarea {
    &,
    &::placeholder {
      @include heading(16px, $fw: 400, $color: $input-placeholder-color);
      text-transform: uppercase;
    }
  }

  &--with-icon {
    position: relative;

    .form__input {
      padding-right: 40px;
    }
  }

  &--phone {
    .form__icon {
      top: calc(50% - 22px / 2);
    }
  }
}

.form__icon {
  position: absolute;
  right: 15px;
  top: calc(50% - 14px / 2);
}

.form__description {
  @include regular($color: $headings-color);
  margin-top: 20px;
  margin-bottom: 0;
  text-align: center;
}
